/**
* Google Fonts
*/
@import url('https://fonts.googleapis.com/css?family=Montserrat|Open+Sans');


/**
* Global styles
*/
* {
	margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
	background: url(./candyhearts.jpg) no-repeat center center fixed;
  	background-size: cover;
  	font-family: 'Open Sans', sans-serif;
  	font-size: 16px;
  	color: #696969; /* Dimgray */
}

h1,
h2 {
	font-family: 'Montserrat', sans-serif;
}

a {
	text-decoration: none;
}

/**
* Flexbox
*/
.flex-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	width: 100%;
}

.flex-item {

}

/**
* Header
*/

.header-content {
	margin: 5px 25px;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.header-brand {

}

.header-logo {
	color: rgba(255,20,147,0.7);
}

.header-social {
}

.header-social-link {
	cursor: pointer;
}

.header-social-img {
	padding-top: 7px;
	width: 150px;
}

/**
* DOB
*/

.dob-match-card {
	background: rgba(255,255,255,0.8); /* Blue #0099cc */
	width: 81%;
	margin: 50px auto;
	padding: 40px 20px;
	border: 1px solid white;
	border-radius: 10px;
	-webkit-box-shadow: 0 0 10px 3px #f1f1f1;
	        box-shadow: 0 0 10px 3px #f1f1f1;
	text-align: center;
}

.dob-match-button {
	background-color: rgba(0,153,204,0.7);
	color: white;
	font-size: 1.1em;
	padding: 10px;
	border-radius: 10px;
	-webkit-box-shadow: 2px 2px 5px 0 #ccc;
	        box-shadow: 2px 2px 5px 0 #ccc;
}

/**
* Form
*/

.dob-form {
	padding: 20px 15px 0;
	font-size: 1.1em;
}

.dob-form input[type=radio]{
	margin: 0 0.2em 0 1em;
	height: 15px;
}

.dob-form-item-1 {
	margin-top: 1em;
	width: 100%;
 	line-height: 1.5em;
 	font-size: 0.9em;
}

.dob-form-item-2 {
	margin-top: 1em;
	width: 100%;
	line-height: 1.5em;
 	font-size: 0.9em;
}

.dob-form-title {
 	text-align: center;
 	margin-bottom: 0.7em;
}

.dob-form-section {
	margin-bottom: 0.8em;
}

.dob-form-label {
	padding-right: 10px;
	display: inline-block;
}

.dob-form-select {
	height: 30px;
	font-size: 0.9em;
}

.dob-form-button {
	width: 100%;
	margin-top: 30px;
}

select {
	margin: 5px;
}

/**
* Media Queries
*/

@media screen and (min-width: 420px){
	.dob-match-card {
		width: 76%;
	}
}

@media screen and (min-width: 670px){
	.dob-match-card {
		width: 71%;
	}
}

@media screen and (min-width: 830px){
	.dob-match-card {
		width: 66%;
	}
		.dob-form-item-1 {
		width: 50%;
	}
}

@media screen and (min-width: 970px){
	.dob-match-card {
		width: 61%;
	}
}

@media screen and (min-width: 1130px){
	.dob-match-card {
		width: 56%;
	}
}




